"use client";

import { useState, useEffect } from "react";
import { supabase } from "@/lib/supabase";
import { X, Loader2, Image as ImageIcon, Save, Trash2, Plus } from "lucide-react";
import { Product } from "@/types";

interface EditProductModalProps {
  product: Product | null; // The product to edit
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
}

export default function EditProductModal({ product, isOpen, onClose, onSuccess }: EditProductModalProps) {
  const [loading, setLoading] = useState(false);
  
  // Form State
  const [formData, setFormData] = useState({
    name: "",
    price: "",
    stock: "",
    description: "",
  });

  // Image State
  const [existingImages, setExistingImages] = useState<string[]>([]); // URLs from DB
  const [newFiles, setNewFiles] = useState<File[]>([]); // New uploads
  const [newPreviews, setNewPreviews] = useState<string[]>([]); // Previews for new uploads

  // Load Data when Product changes
  useEffect(() => {
    if (product) {
      setFormData({
        name: product.name,
        price: product.price.toString(),
        stock: product.stock_quantity.toString(),
        description: product.description || "",
      });
      setExistingImages(product.image_urls || []);
      setNewFiles([]);
      setNewPreviews([]);
    }
  }, [product]);

  if (!isOpen || !product) return null;

  // --- HANDLERS ---

  // 1. Handle New File Selection
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const selected = Array.from(e.target.files);
      const totalImages = existingImages.length + newFiles.length + selected.length;
      
      if (totalImages > 4) {
        alert("You can only have up to 4 images per product.");
        return;
      }

      setNewFiles([...newFiles, ...selected]);
      
      const previews = selected.map(file => URL.createObjectURL(file));
      setNewPreviews([...newPreviews, ...previews]);
    }
  };

  // 2. Remove Existing Image (From DB List)
  const removeExistingImage = (indexToRemove: number) => {
    setExistingImages(prev => prev.filter((_, i) => i !== indexToRemove));
  };

  // 3. Remove New File (From Upload List)
  const removeNewFile = (indexToRemove: number) => {
    setNewFiles(prev => prev.filter((_, i) => i !== indexToRemove));
    setNewPreviews(prev => prev.filter((_, i) => i !== indexToRemove));
  };

  // 4. Submit Update
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const finalImageUrls = [...existingImages]; // Start with the old ones we kept

      // Upload New Files
      for (const file of newFiles) {
        const fileName = `${product.store_id}/${Date.now()}-${Math.random().toString(36).substring(7)}`;
        const { error: uploadError } = await supabase.storage.from("products").upload(fileName, file);
        if (uploadError) throw uploadError;

        const { data } = supabase.storage.from("products").getPublicUrl(fileName);
        finalImageUrls.push(data.publicUrl);
      }

      // Update Database
      const { error } = await supabase
        .from("products")
        .update({
          name: formData.name,
          price: parseFloat(formData.price),
          stock_quantity: parseInt(formData.stock),
          description: formData.description,
          image_urls: finalImageUrls, // Save the combined list
        })
        .eq("id", product.id);

      if (error) throw error;

      onSuccess();
      onClose();
    } catch (error: any) {
      alert("Error: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
      <div className="bg-white w-full max-w-lg rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10 flex flex-col max-h-[90vh]">
        
        <div className="p-5 border-b border-gray-100 flex justify-between items-center bg-gray-50">
          <h2 className="font-bold text-lg text-gray-900">Edit Product</h2>
          <button onClick={onClose} className="p-2 bg-white rounded-full shadow-sm text-gray-500 hover:bg-gray-100"><X size={20} /></button>
        </div>

        <div className="p-6 overflow-y-auto">
          <form onSubmit={handleSubmit} className="space-y-5">
            
            {/* --- IMAGE MANAGER --- */}
            <div>
              <label className="block text-sm font-bold text-gray-700 mb-2">Images ({existingImages.length + newFiles.length}/4)</label>
              <div className="grid grid-cols-4 gap-2">
                
                {/* 1. Existing Images */}
                {existingImages.map((url, idx) => (
                  <div key={`old-${idx}`} className="aspect-square relative rounded-xl overflow-hidden border border-gray-200 group">
                    <img src={url} alt="Product" className="w-full h-full object-cover" />
                    <div className="absolute inset-0 bg-black/20 opacity-0 group-hover:opacity-100 transition flex items-center justify-center">
                       <button type="button" onClick={() => removeExistingImage(idx)} className="bg-red-500 text-white p-1 rounded-full"><Trash2 size={14}/></button>
                    </div>
                  </div>
                ))}

                {/* 2. New Previews */}
                {newPreviews.map((url, idx) => (
                  <div key={`new-${idx}`} className="aspect-square relative rounded-xl overflow-hidden border-2 border-green-500">
                    <img src={url} alt="New" className="w-full h-full object-cover" />
                    <button type="button" onClick={() => removeNewFile(idx)} className="absolute top-1 right-1 bg-red-500 text-white p-1 rounded-full shadow-sm"><X size={12}/></button>
                  </div>
                ))}

                {/* 3. Upload Button */}
                {(existingImages.length + newFiles.length) < 4 && (
                  <label className="aspect-square rounded-xl border-2 border-dashed border-gray-300 flex flex-col items-center justify-center text-gray-400 bg-gray-50 cursor-pointer hover:border-gray-900 hover:text-gray-900 transition">
                    <Plus size={24} className="mb-1"/>
                    <span className="text-[10px] font-bold">Add</span>
                    <input type="file" accept="image/*" multiple onChange={handleFileChange} className="hidden" />
                  </label>
                )}
              </div>
            </div>

            {/* --- DETAILS FORM --- */}
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-bold text-gray-700 mb-1">Product Name</label>
                <input required className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl outline-none focus:ring-2 focus:ring-gray-900" value={formData.name} onChange={e => setFormData({...formData, name: e.target.value})} />
              </div>

              <div className="grid grid-cols-2 gap-4">
                 <div>
                    <label className="block text-sm font-bold text-gray-700 mb-1">Price (₦)</label>
                    <input required type="number" className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl outline-none focus:ring-2 focus:ring-gray-900" value={formData.price} onChange={e => setFormData({...formData, price: e.target.value})} />
                 </div>
                 <div>
                    <label className="block text-sm font-bold text-gray-700 mb-1">Stock Quantity</label>
                    <input required type="number" className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl outline-none focus:ring-2 focus:ring-gray-900" value={formData.stock} onChange={e => setFormData({...formData, stock: e.target.value})} />
                 </div>
              </div>

              <div>
                <label className="block text-sm font-bold text-gray-700 mb-1">Description</label>
                <textarea className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl h-24 resize-none outline-none focus:ring-2 focus:ring-gray-900" value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} />
              </div>
            </div>

            <button type="submit" disabled={loading} className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold shadow-lg flex items-center justify-center gap-2">
              {loading ? <Loader2 className="animate-spin" /> : <><Save size={18} /> Update Product</>}
            </button>

          </form>
        </div>
      </div>
    </div>
  );
}