"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { useRouter } from "next/navigation";
import { BadgeCheck, Plus, Package, Loader2, Settings, Edit2, Trash2, Eye, Box, CreditCard, Activity } from "lucide-react";
import Image from "next/image";
import { Store, Product } from "@/types";

// --- IMPORT ALL MODALS ---
import AddProductModal from "@/components/store/AddProductModal";
import EditProfileModal from "@/components/store/EditProfileModal";
import EditProductModal from "@/components/store/EditProductModal";

export default function Dashboard() {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  
  // Data State
  const [store, setStore] = useState<Store | null>(null);
  const [products, setProducts] = useState<Product[]>([]);
  
  // Modal States
  const [isAddProductOpen, setIsAddProductOpen] = useState(false);
  const [isEditProfileOpen, setIsEditProfileOpen] = useState(false);
  const [editingProduct, setEditingProduct] = useState<Product | null>(null); // Holds the product being edited

  // --- LOAD DATA ---
  const loadDashboardData = async () => {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) { router.push("/login"); return; }

    // 1. Get Store
    const { data: userStore } = await supabase.from("stores").select("*").eq("owner_id", user.id).single();
    if (!userStore) { router.push("/onboarding"); return; }
    setStore(userStore);

    // 2. Get Products (Newest First)
    const { data: storeProducts } = await supabase
      .from("products")
      .select("*")
      .eq("store_id", userStore.id)
      .order('created_at', { ascending: false });
      
    setProducts(storeProducts || []);
    setLoading(false);
  };

  useEffect(() => { loadDashboardData(); }, [router]);

  // --- DELETE PRODUCT ---
  const deleteProduct = async (id: string) => {
    if(!confirm("Are you sure you want to delete this product? This cannot be undone.")) return;
    
    // Optional: You could also delete the images from storage here to save space
    
    await supabase.from("products").delete().eq("id", id);
    loadDashboardData(); // Refresh list
  };

  if (loading) return <div className="min-h-screen flex items-center justify-center bg-gray-50"><Loader2 className="animate-spin text-gray-400" /></div>;
  if (!store) return null;

  return (
    <div className="min-h-screen bg-gray-50 pb-32">
      
      {/* --- TOP BAR --- */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-20">
        <div className="max-w-6xl mx-auto px-4 h-16 flex items-center justify-between">
          <div className="font-bold text-xl tracking-tight flex items-center gap-2">
            StoreLink <span className="bg-gray-100 text-xs px-2 py-1 rounded-md text-gray-500 font-normal">Vendor</span>
          </div>
          <div className="flex items-center gap-3">
             <button onClick={() => setIsEditProfileOpen(true)} className="text-sm font-medium text-gray-600 hover:text-gray-900 flex items-center gap-2 bg-gray-100 px-3 py-2 rounded-lg transition hover:bg-gray-200">
                <Settings size={16} /> Edit Profile
             </button>
          </div>
        </div>
      </header>

      <main className="max-w-6xl mx-auto px-4 py-8">
        
        {/* --- WELCOME CARD --- */}
        <div className="bg-white rounded-3xl p-6 border border-gray-100 shadow-sm mb-8 relative overflow-hidden">
          {/* Decorative background blob */}
          <div className="absolute top-0 right-0 w-64 h-64 bg-green-50 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2 opacity-50 pointer-events-none"></div>
          
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 relative z-10">
             <div className="flex items-center gap-4">
                {/* Logo Circle */}
                <div className="w-16 h-16 rounded-full bg-gray-200 overflow-hidden border-2 border-white shadow-sm relative">
                  {store.logo_url ? <Image src={store.logo_url} alt="Logo" fill className="object-cover" /> : <div className="w-full h-full flex items-center justify-center text-gray-400 font-bold text-xl">{store.name[0]}</div>}
                </div>
                
                <div>
                  <p className="text-gray-500 text-xs font-bold uppercase tracking-wider mb-0.5">Welcome Back</p>
                  <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-2">
                    {store.name}
                    {store.subscription_plan === 'premium' && <BadgeCheck className="text-blue-500 fill-blue-50 w-6 h-6" />}
                  </h1>
                </div>
             </div>
             
             <div className="flex gap-3 w-full md:w-auto mt-2 md:mt-0">
               <a href={`/${store.slug}`} target="_blank" className="flex-1 md:flex-none text-center px-5 py-3 bg-white border border-gray-200 text-gray-700 rounded-xl text-sm font-bold hover:bg-gray-50 transition shadow-sm">
                 View Live Shop
               </a>
               <button onClick={() => setIsAddProductOpen(true)} className="flex-1 md:flex-none px-6 py-3 bg-gray-900 text-white rounded-xl text-sm font-bold shadow-lg hover:bg-gray-800 transition flex items-center justify-center gap-2">
                 <Plus size={18} /> Add Product
               </button>
             </div>
          </div>
        </div>

        {/* --- STATS GRID --- */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-10">
           {/* Total Products */}
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-32 hover:border-gray-300 transition group">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-purple-50 rounded-lg text-purple-600 group-hover:bg-purple-100 transition"><Box size={20} /></div>
                 <span className="text-[10px] md:text-xs text-gray-400 font-bold tracking-wider">PRODUCTS</span>
              </div>
              <p className="text-3xl font-bold text-gray-900">{products.length}</p>
           </div>

           {/* Total Views */}
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-32 hover:border-gray-300 transition group">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-orange-50 rounded-lg text-orange-600 group-hover:bg-orange-100 transition"><Eye size={20} /></div>
                 <span className="text-[10px] md:text-xs text-gray-400 font-bold tracking-wider">VIEWS</span>
              </div>
              <p className="text-3xl font-bold text-gray-900">0</p>
           </div>

           {/* Plan */}
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-32 hover:border-gray-300 transition group">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-blue-50 rounded-lg text-blue-600 group-hover:bg-blue-100 transition"><CreditCard size={20} /></div>
                 <span className="text-[10px] md:text-xs text-gray-400 font-bold tracking-wider">PLAN</span>
              </div>
              <p className="text-2xl font-bold text-gray-900 capitalize">{store.subscription_plan}</p>
           </div>

           {/* Status */}
           <div className="bg-white p-5 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-32 hover:border-gray-300 transition group">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-green-50 rounded-lg text-green-600 group-hover:bg-green-100 transition"><Activity size={20} /></div>
                 <span className="text-[10px] md:text-xs text-gray-400 font-bold tracking-wider">STATUS</span>
              </div>
              <p className="text-2xl font-bold text-green-600">Active</p>
           </div>
        </div>

        {/* --- INVENTORY HEADER --- */}
        <div className="flex items-center justify-between mb-4">
           <h2 className="text-lg font-bold text-gray-900">Inventory</h2>
           <span className="text-xs text-gray-400 font-medium">{products.length} Items</span>
        </div>
        
        {/* --- PRODUCT LIST --- */}
        {products.length > 0 ? (
          <div className="bg-white rounded-2xl border border-gray-100 overflow-hidden shadow-sm">
            {products.map((product, index) => (
              <div key={product.id} className={`flex flex-col sm:flex-row sm:items-center gap-4 p-4 hover:bg-gray-50 transition ${index !== products.length - 1 ? 'border-b border-gray-50' : ''}`}>
                
                {/* 1. Image (First from the list) */}
                <div className="h-20 w-20 bg-gray-100 rounded-xl relative overflow-hidden flex-shrink-0 border border-gray-200">
                   {product.image_urls && product.image_urls.length > 0 ? (
                     <Image src={product.image_urls[0]} alt="" fill className="object-cover" />
                   ) : (
                     <div className="w-full h-full flex items-center justify-center text-gray-300"><Package size={20} /></div>
                   )}
                </div>
                
                {/* 2. Info */}
                <div className="flex-1 min-w-0">
                   <h3 className="font-bold text-gray-900 text-sm truncate">{product.name}</h3>
                   <div className="flex items-center gap-2 mt-1">
                     <p className="text-emerald-600 font-bold text-sm">₦{product.price.toLocaleString()}</p>
                     <span className="text-gray-300">•</span>
                     {product.stock_quantity > 0 ? (
                        <span className="text-xs bg-green-50 text-green-700 px-2 py-0.5 rounded-md font-medium">In Stock ({product.stock_quantity})</span>
                     ) : (
                        <span className="text-xs bg-red-50 text-red-600 px-2 py-0.5 rounded-md font-medium">Out of Stock</span>
                     )}
                   </div>
                </div>

                {/* 3. Actions */}
                <div className="flex gap-2 self-end sm:self-center">
                   <button 
                      onClick={() => setEditingProduct(product)} 
                      className="flex items-center gap-2 px-3 py-2 bg-gray-100 hover:bg-blue-50 text-gray-600 hover:text-blue-600 rounded-lg text-xs font-bold transition"
                   >
                     <Edit2 size={16} /> Edit
                   </button>
                   <button 
                      onClick={() => deleteProduct(product.id)} 
                      className="p-2 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-lg transition"
                   >
                      <Trash2 size={18} />
                   </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          // Empty State
          <div className="bg-white rounded-3xl border border-dashed border-gray-200 p-12 text-center flex flex-col items-center">
             <div className="w-16 h-16 bg-gray-50 rounded-full flex items-center justify-center mb-4">
               <Package className="text-gray-300 w-8 h-8" />
             </div>
             <h3 className="text-lg font-bold text-gray-900">Your shelf is empty</h3>
             <p className="text-gray-500 text-sm max-w-xs mx-auto mt-2">
               Upload your first product to start selling today.
             </p>
             <button onClick={() => setIsAddProductOpen(true)} className="mt-6 px-6 py-2 bg-gray-900 text-white rounded-xl text-sm font-bold shadow-lg hover:bg-gray-800 transition">
               Add First Product
             </button>
          </div>
        )}

      </main>

      {/* --- MODALS --- */}
      
      {/* 1. Add Product */}
      <AddProductModal 
        storeId={store.id} 
        isOpen={isAddProductOpen} 
        onClose={() => setIsAddProductOpen(false)}
        onSuccess={loadDashboardData}
      />
      
      {/* 2. Edit Profile */}
      <EditProfileModal 
        store={store}
        isOpen={isEditProfileOpen}
        onClose={() => setIsEditProfileOpen(false)}
        onSuccess={loadDashboardData}
      />

      {/* 3. Edit Product (Godmode) */}
      <EditProductModal 
        product={editingProduct}
        isOpen={!!editingProduct} 
        onClose={() => setEditingProduct(null)}
        onSuccess={loadDashboardData}
      />

    </div>
  );
}